import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";
import human from "images/human.png";
import book from "images/book.png";
import { Link, useNavigate } from "react-router-dom";
import Container from "@mui/material/Container";
import eventsStore from "stores/events-store";
import { observer } from "mobx-react-lite";
import EventMap from "components/EventMap";

type CategoryType = {
  id: string;
  name: string;
};

const categories: CategoryType[] = [
  { id: "subject_1", name: " " },
  { id: "subject_2", name: "  " },
  { id: "subject_3", name: " " },
  { id: "subject_4", name: " " },
  { id: "subject_5", name: "  " },
  { id: "subject_6", name: "  " },
  { id: "subject_7", name: " " },
  { id: "subject_8", name: " " },
];

const Main = observer(() => {
  const handleCategoryClick = (category: CategoryType) => {
    if (!eventsStore.isActiveFilter(category.id)) {
      eventsStore.addFilter(category.id);
    } else {
      eventsStore.removeFilter(category.id);
    }
  };

  const navigate = useNavigate();

  return (
    <Container>
      <Box display="flex" flexDirection="column" alignItems="center">
        <img src={human} alt="  " width={300} />
        <Typography
          variant="h2"
          textAlign="center"
          fontWeight="bold"
          gutterBottom
        >
          <span style={{ color: "#5A3DFF" }}>13895</span>   
          
        </Typography>
        <Typography color="text.secondary" gutterBottom>
               .    
           
        </Typography>
        <Stack
          direction="row"
          justifyContent="center"
          alignItems="center"
          spacing={2}
          flexWrap="wrap"
          gap={1}
          padding={2}
        >
          <Button variant="contained" onClick={() => navigate("/events")}>
             
          </Button>
          <Button variant="contained" onClick={() => navigate("/personalArea")}> </Button>
        </Stack>
        <Typography variant="h3" sx={{ paddingTop: 5 }}>
              
        </Typography>
        <Stack
          direction="row"
          justifyContent="center"
          alignItems="center"
          spacing={2}
          flexWrap="wrap"
          gap={1}
          padding={2}
        >
          {categories.map((x, i) => (
            <Button
              key={i}
              variant={
                eventsStore.isActiveFilter(x.id) ? "contained" : "outlined"
              }
              sx={{ borderRadius: 10 }}
              onClick={() => handleCategoryClick(x)}
            >
              {x.name}
            </Button>
          ))}
        </Stack>
        {eventsStore.errorMessage && (
          <Typography> : {eventsStore.errorMessage}</Typography>
        )}
        {!eventsStore.errorMessage && <EventMap data={eventsStore.data} />}
        <Box
          padding={10}
          display="flex"
          flexDirection="column"
          alignItems="center"
        >
          <img src={book} alt="   ?" width={160} />
          <Typography variant="h4">   ?</Typography>
          <Typography>
                   ,  
            
          </Typography>
          <Link to="/onboarding">
            <Button sx={{ mt: 2, mb: 1, p: 2 }} variant="contained">
               
            </Button>
          </Link>
          <Typography variant="caption" color="text.secondary">
             3 
          </Typography>
        </Box>
      </Box>
    </Container>
  );
});

export default Main;
